import React from "react";
import './Login.css';
import { loginUrl } from "./mitvos";



function Login(){
    return (
        <div className="login" >

            
            <img src="https://i.pinimg.com/originals/d7/c0/8b/d7c08ba221d859444203bf72e969a95a.png" 
                alt=""/>
            
           
            {/* Login with Spotify button */}
            <a href = {loginUrl}>LOGIN WITH MITVOS</a>
            <p className="loginheading">MITVOS stands for music is the voice of soul,this is a clone of spotify app.</p> 
            <p className="loginheading">Made with  in India </p>
            
        </div>
    )
}
export default Login;