import React from "react"
import "./Sidebar.css"
import SidebarOptions from "./SidebarOptions";
import HomeIcon from '@mui/icons-material/Home';
import SearchIcon from '@mui/icons-material/Search';
import LibraryMusicIcon from '@mui/icons-material/LibraryMusic';
import { useDataLayerValue } from "./DataLayer";

function Sidebar(){
    const [{playlists}, dispatch] = useDataLayerValue()


    return(
        <div className="sidebar">
          <img className= "logo" src="https://www.pngkey.com/png/full/190-1907978_spotify-logo-png-white-spotify-logo-white-transparent.png" alt="" /> 
        
          <SidebarOptions Icon={HomeIcon} option="Home" />
          <SidebarOptions Icon={SearchIcon} option="Search" />
          <SidebarOptions Icon={LibraryMusicIcon} option="Your Library" />
        <br />
        <strong className="sidebar__options">PLAYLISTS</strong>

        <hr/>
        {playlists?.items?.map(playlist => (
            <SidebarOptions option={playlist.name} />
        ))}
        
        <SidebarOptions option='Hip hop' />
        <SidebarOptions option='Rock' />
        <SidebarOptions option='Classical' />
        </div>

    );

}

export default Sidebar ;